body {
    background: rgb(37, 30, 34);
}
header {
    position: absolute;
    top: 2%;
    left: 18px;
}
h1 {
    margin-top: 0;
}
.menu {
    padding-left: 15px;
}

.canvasWrap {
    background: #000;
    width: 1008px;
    height: 567px;
    margin-left: -504px;  /* half w*/
    position: absolute;
    left: 50%;
    top: 10%;
    box-shadow: 0 0 10px 3px #222;
}
.canvasWrap .minW {
    display: none;
}
.canvasWrap canvas {
    margin: 0 auto;
    background: #000;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
}
.preTransition{
    opacity: 1;
}
.duringTransition {
    opacity: 0.01;
}

/* Colorboxes */
.gameInstructions {
    display: none;
}
#cboxLoadedContent {
    background: rgb(37, 30, 34);
    padding: 10px;
}
#cboxLoadedContent h2 {
	margin-top: 0;
}
.goals {
	width: 180px;
}
button {
	padding: 6px 5px 3px 6px;
	border: 1px inset;
	border-radius: 2px;
	background-color: #ddd;
}
button:hover {
	background-color: #eee;
}
button:active {
	border: 1px outset;
}
button span {
	font-size: 1.2em;
	margin-top: -1px;
}
.off {
	border-color: red;
	box-shadow: 0 0 8px red;
}
.off:hover {
	box-shadow: 0 0 17px red;
}
.on {
	border-color: #0ff;
	box-shadow: 0 0 8px #0ff;
}
.on:hover {
	box-shadow: 0 0 14px #0ff;
}
.resize {
    margin-top: 10px;
}
input[readonly] {
	width: 20px;
	font-size: 1em;
	background-color: transparent; 
	color: #e1e1e1; 
	text-align: center;
}
.keyAction {
	width: 120px; 
	display: inline-block;
}
.goals {
	margin-bottom: 20px;
}
.padBot {
    margin-bottom: 18px;
}

@media screen and (max-width: 1323px) {
    header {
        position: static;
        text-align: center;
    }
    .canvasWrap {
        top: 95px;
    }
}
@media screen and (max-width: 1007px) {
    .canvasWrap {
        position: static;
        margin: 20px auto;
        width: 75%;
        box-shadow: none;
        text-align: center;
    }
    .canvasWrap .minW {
        display: block;
        padding-top: 10%;
    }
    .canvasWrap canvas {
        display: none;
    }
}

.fullscreen {
    overflow: hidden;
    background: #000;
}
.fullscreen header {
    display: none;
}
.fullscreen .canvasWrap {
    top: 0;
    box-shadow: none;
}
.fullscreen canvas {
    width: 100%;
}
